.signin-form  {
  padding: 10px;
  form {
    padding-top:10px;
  }
  p {
    padding-bottom:5px;
  }
  a {
    font-size:12px;
  }
}

.step-links a {
  text-decoration: none;
}

ul.errorlist {
  padding: 0;
  margin-bottom: 16px;
  li {
    display: inline-block;
  }
}

.error, .errorlist {
  color: #ff4b4b !important;
  list-style: none;
  margin: 0;
  display: block;
  padding: 0;
  li {
    display: inline-block;
  }
}

.error:before, .errorlist li:before, p.errorlist:before {
  content: " ";
  display: inline-block;
  background: red;
  width: 16px;
  height: 16px;
  background: url('data:image/png;base64,');
  background-size: 16px;
  background-repeat: no-repeat;
  background-position: 50%;
  vertical-align: middle;
}
